<template>
    <div id="map">
        
    </div>
</template>

<script>
import axios from 'axios';

export default {

    name: 'Map',     
    data() {
       return {
             lat: 37.2683492,
             lng: 127.1365699,
             m: 5000, //Í∏∞Î≥∏ Î∞òÍ≤Ω 1km 
             findMask: {
                 address: '',
                 count:'',
                 stores: []
             },             
        }
    },
    mounted() {
        this.getLocation();
    },
    methods: {
        getStoresByGeo(position){
            this.lat = position.coords.latitude;
            this.lng = position.coords.longitude;
            axios.get(`https://8oi9s0nnth.apigw.ntruss.com/corona19-masks/v1/storesByGeo/json?lat=${this.lat}&lng=${this.lng}&m=${this.m}`).then(res => {
                    this.findMask = res.data;
                    this.drawMap(position, this.findMask)

                 }).catch(err => {
                     console.log(err);
            });
        },
        getLocation() {
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(this.onSuccessGeolocation, this.onErrorGeolocation);
            } else {
                alert("error");
            }
        },
        onSuccessGeolocation(position) {

            this.getStoresByGeo(position);           
        
        },
        onErrorGeolocation(){
            var mapContainer = document.getElementById('map'); // ÏßÄÎèÑÎ•º ÌëúÏãúÌï† div 
            
            var mapOption = { 

                center: new kakao.maps.LatLng(33.450701, 126.570667), // ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú
                level: 5 // ÏßÄÎèÑÏùò ÌôïÎåÄ Î†àÎ≤®
            };

            // ÏßÄÎèÑÎ•º ÌëúÏãúÌï† divÏôÄ  ÏßÄÎèÑ ÏòµÏÖòÏúºÎ°ú  ÏßÄÎèÑÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
            var map = new kakao.maps.Map(mapContainer, mapOption); 
        },
        drawMap(position, findMask){
            console.log(position);
 
            if(findMask.stores.length!=0){

                //Í≤ÄÏÉâ Îç∞Ïù¥ÌÑ∞ÏóêÏÑú Lat, Lng Ïùò Ï§ëÍ∞Ñ Í∞í
                let latAve=0;
                let LngAve=0; 
                
                let len = findMask.stores.length;
                for(let i=0; i<len; i++){
                    latAve+=findMask.stores[i].lat;
                    LngAve+=findMask.stores[i].lng;

                }
                latAve=latAve/len;
                LngAve=LngAve/len;
                let mapContainer = document.getElementById('map');// ÏßÄÎèÑÎ•º ÌëúÏãúÌï† div 
                let mapOption = { 
                        center: new kakao.maps.LatLng(latAve, LngAve), // ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú
                        level: 5 // ÏßÄÎèÑÏùò ÌôïÎåÄ Î†àÎ≤®
                    };


                // ÏßÄÎèÑÎ•º ÌëúÏãúÌï† divÏôÄ  ÏßÄÎèÑ ÏòµÏÖòÏúºÎ°ú  ÏßÄÎèÑÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
                let map = new kakao.maps.Map(mapContainer, mapOption); 

                for(let i=0; i<len; i++){
                    //HTML Î¨∏ÏûêÏó¥ ÎòêÎäî Dom Element ÏûÖÎãàÎã§
                    let state = this.getRemainStat(findMask.stores[i].remain_stat);
                    let content = '<div class="find-mask-here">';
                        content += '        </span><span class="number">' + state+'</span>';
                        content += '</div>';
                    let contentPosition  = new kakao.maps.LatLng(findMask.stores[i].lat, findMask.stores[i].lng); 
                    // ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
                    let marker = new kakao.maps.CustomOverlay({
                        position: contentPosition,
                        content: content
                    });
                    // ÎßàÏª§Í∞Ä ÏßÄÎèÑ ÏúÑÏóê ÌëúÏãúÎêòÎèÑÎ°ù ÏÑ§Ï†ïÌï©ÎãàÎã§
                    marker.setMap(map);


                } 

            }      
        },
        getRemainStat(remain_stat){
            let status = '';
            /*
             * Ïû¨Í≥† ÏÉÅÌÉú[100Í∞ú Ïù¥ÏÉÅ(ÎÖπÏÉâ): 'plenty' / 
             * 30Í∞ú Ïù¥ÏÉÅ 100Í∞úÎØ∏Îßå(ÎÖ∏ÎûëÏÉâ): 'some' / 
             * 2Í∞ú Ïù¥ÏÉÅ 30Í∞ú ÎØ∏Îßå(Îπ®Í∞ïÏÉâ): 'few' / 
             * 1Í∞ú Ïù¥Ìïò(ÌöåÏÉâ): 'empty' / 
             * ÌåêÎß§Ï§ëÏßÄ: 'break'
            */
           switch(remain_stat){
               case 'plenty':
                   status = '100Í∞ú Ïù¥ÏÉÅ';
                   break;
               case 'some':
                   status = '30Í∞ú Ïù¥ÏÉÅ 100Í∞úÎØ∏Îßå';
                   break;
               case 'few':
                   status = '1Í∞ú Ïù¥ÏÉÅ 30Í∞ú ÎØ∏Îßå';
                   break;
               case 'empty':
                   status = 'ÏóÜÏùå';
                   break;
               case 'break':
                   status = 'ÌåêÎß§Ï§ëÏßÄ';
                   break;
           }

            return status;
        }
        


    }    
};
</script>

<style>
#map {
    width:100%;
    height:750px;
}

.find-mask-here {
    background-color: azure;
    border: 2px solid #73AD21;
    border-radius: 5px;

}
.find-mask-here li {
    text-align: left;
}
.find-mask-here {
    z-index:555555 !important;
}

.find-mask-here .number {
    font-size: 15px;
    font-family: NotoSansCJKkr-Thin;
}


</style>


